  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background: rgba(0, 20, 20, .8);
  transition: all .5s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  z-index: 30;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 480px;
  text-align: center;
  background-color: #01383a;
  padding: 100px 0;
  transition: all .5ms linear;
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
